<template>
  <BackNavbar></BackNavbar>
  <div class="container">
    <router-view></router-view>
  </div>
</template>
<script>
import BackNavbar from "@/components/BackNavbar.vue";
export default {
  components: {
    BackNavbar,
  },
  created() {
    const backLoginCookie = document.cookie.replace(
      /(?:(?:^|.*;\s*)backLoginToken\s*\=\s*([^;]*).*$)|^.*$/,
      "$1"
    );
    this.$http.defaults.headers.common["Authorization"] = backLoginCookie;
    const api  =  `${process.env.VUE_APP_API_PATH}/api/user/check`
    this.$http.post(api).then((res)=>{
        if(!res.data.success) {
          this.$router.push('/backLogin')
        }
    })

    
  },
};
</script>
